.project-heading {
    text-align: center;
    padding: 4rem 0 2rem 0;
}

.project-img{
    width: 100%;
    height: 200px;
}

.project-container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-gap: 40px;
    padding-bottom: 5rem;
}

.project-card{
    padding: 1.2rem 1rem;
    box-shadow: 8px 14px 38px rgba(39,44,49,.06), 1px 3px 8px rgba(39,44,49,.03);
  transition: all .5s ease;
}

.project-card:hover{
    transform: translate3D(0,-2px,0) scale(1.05);
    box-shadow: 8px 28px 50px rgba(39,44,49,.07), 1px 6px 12px rgba(39,44,49,.04);
    transition: all .4s ease;
}

.project-card img{
    width: 100%;
}


.project-title{
    color:#fff;
    padding: 1rem 0;

}

.pro-details p{
    padding-bottom: 1rem;
    font-size: 1rem;
    text-align: justify;
}


.pro-btns{
    display: flex;
    justify-content: space-between;
    padding: 0.5rem 0;
  
}

.pro-btns .btn{
    position: static;
    height: 2.5rem;
    width: 20rem;
    padding: 0.5rem 1rem;
    text-align: center;
    margin: 0;
}

@media screen and (max-width: 640px){
    
    .project-container{
        max-width: 90%;
        margin: auto;
        grid-template-columns: repeat(1,1fr);
    }
}
